<template>
    <div id="blog-form">
			
			<a @click.prevent="scrollToTop" id="btnscrollTop">
				<font-awesome-icon class="color-icon-up" icon="chevron-circle-up"/>
			</a>		
			
			<div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Inheritance</div>
      
      <section>
        <div class="vl-solmn-inner">
          <div class="wb-wrapper">
            <div class="container mx-auto px-9 lg:px-69 cont-contf">
              <h1>Inheritance plays an important role in object oriented programming, because it allows us to build classes and than reuse that classe in a definition of another class. Lets say we have a class motor vehicle and than we diside we want a specialization of that a car or automobile class, or than later on we might want a truck class which is again a specialization of motor vehicle. Being able to use inheritance allows us to reuse code quate a bit because a specialization of a original class, called a base class can use the code from the base class over and over again. So things that is common to all the classes we can define once and not have define them again in a specialization which is also called a derived class. </h1>
              <br>
              <h1>So begin a class definition we start outside of <code>int main()</code></h1>
              <br>
<pre class="result notranslate">
<span class="b">class</span> <span class="g">Employee</span>  {

  <span class="b">private</span>:
   <span class="g">string</span> name;
   <span class="b">double</span> pay;
  
  <span class="b">public</span>:
   <span class="y">Employee</span>() : name{ "" }, pay{ 0.0 } {}
   <span class="y">Employee</span>(<span class="g">string</span> name, <span class="b">double</span> pay) : name{ name }, pay{ pay } {}
  
  <span class="b">public</span>:
    <span class="g">string</span> getName(){
      <span class="p">return</span> name;
    }
    
    <span class="b">double</span> getPay(){
      <span class="p">return</span> pay;
    }
    
    <span class="b">void</span> setName(<span class="g">string</span> name){
      <span class="b">this</span>->name = name;
    }
    
    <span class="b">void</span> setPay(<span class="b">double</span> pay){
      <span class="b">this</span>->pay = pay;
    }
};

</pre>
              
<h1>This class Employee will now become the Base class. For a set of specializations or derived classes that we can use to define a different types of Employes in a company. For example we can have a hourly employes and managers. Any classes that are created from Employee class are called derived classes.</h1>
              <br>
<h1>So lets create a Manager class, which is an example of derived class. So we begin by definding a class Manager followed by a colomn. The colomn indicates that we are about to indicate what is derived class is.</h1>
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Manager</span> : <span class="b">public</span> <span class="y">Employee</span> {

  <span class="b">private</span>:
   <span class="b">bool</span> salaried;
};

</pre>
              
<h1>The only data member we are going to define now for Manager class is going to be boolean variable salaried which indicate whether or not the Manager is a salaried employee or not.</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Manager</span> : <span class="b">public</span> <span class="y">Employee</span> {

  <span class="b">private</span>:
   <span class="b">bool</span> salaried;
   
  <span class="b">public</span>:
   <span class="y">Manager</span>(<span class="g">string</span> name, <span class="b">double</span> pay, <span class="b">bool</span> isSalaried) : <span class="y">Employee</span>(name, pay), salaried{ isSalaried } {}
};

</pre>
<h1>We are definding a constructor Manager followed by the parameter list which will include parameters for the name, payrate and salaried variable. We are going to use the constructor we created for the Employee class in our manager class we can call that in our Manager constructor by using the column again.</h1>
<pre class="result notranslate">
   <span class="y">Manager</span>(<span class="g">string</span> name, <span class="b">double</span> pay, <span class="b">bool</span> isSalaried) : <span class="y">Employee</span>(name, pay), salaried{ isSalaried } {}
</pre>
<h1>So after we have defined a parameter list for Manager we call the Employee constructor by using a keyword Employee followed by the parameters that are passing to it name and pay.</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Manager</span> : <span class="b">public</span> <span class="y">Employee</span> {
<span class="b">private</span>:
 <span class="b">bool</span> salaried;
<span class="b">public</span>:
	<span class="y">Manager</span>(<span class="g">string</span> name, <span class="b">double</span> pay, <span class="b">bool</span> isSalaried) : <span class="y">Employee</span>(name, pay), salaried{ isSalaried } {}

	<span class="b">bool</span> getSalaried() {
		<span class="p">return</span> salaried;
	}
};
</pre>
               
<h1>So to create a derived class you use class in the name of a derived class followed by a : followed by the keyword public and the name of the base class that you are deriving from.</h1>          
<pre class="result notranslate">
<span class="b">class</span> <span class="y">Manager</span> : <span class="b">public</span> <span class="y">Employee</span> {
};
</pre>    
              
              <br>
              <hr>
              <div class="text-2xl mb-4 md:mb-12 font-bold flex justify-center items-center our-loc">Example programm</div>
              
<h1>In this chapter we will be demonstrating Inheritance by creating a Base class called Account and a Derived class called Checking Account and demonstrate how we can use Inheritance to minimize the amount of work we have to do to create the derived class Checking Account from the Base class Account.</h1>
<br>
<h1>Our Base class is Account that will have one protected field balance. First public function will be the constructor and constractor will simply check to see is the balance entered is greater that 0. Is to prevent creating an account with a negative numbers.</h1>

<pre class="result notranslate">
<span class="b">class</span> <span class="y">Account</span> {
<span class="b">protected</span>:
  <span class="b">double</span> balance;
<span class="b">public</span>:
  <span class="y">Account</span>(<span class="b">double</span> bal) {
     <span class="p">if</span>(bal > 0){
      balance = bal;
     }<span class="p">else</span>{
      balance = 0;
     }
    }
    
  <span class="b">void</span> credit(<span class="b">double</span> amount){
    balance += amount;
  }
  
  <span class="b">void</span> debit(<span class="b">double</span> amount){
    <span class="p">if</span>(balance > amount){
      balance -= amount;
    }<span class="p">else</span>{
      // Insufficient funds.
    }
  }
  
  <span class="b">double</span> getBalance(){
    <span class="p">return</span> balance;
  }
};
</pre>
              
<h1>Creating a Checking Account derived class.</h1>
              
<pre class="result notranslate">
<span class="b">class</span> <span class="y">CheckingAccount</span> : <span class="b">public</span> <span class="y">Account</span> {
<span class="b">private</span>:
  <span class="b">double</span> fee;
<span class="b">public</span>:
  <span class="y">CheckingAccount</span>(<span class="b">double</span> bal, <span class="b">double</span> fee) : <span class="y">Account</span>(bal){
     <span class="b">this</span>->fee = fee;
    }
  
  <span class="b">void</span> debit(<span class="b">double</span> amount){
    <span class="p">if</span>(balance > amount){
      balance = balance - amount - fee;
    }<span class="p">else</span>{
      // Insufficient funds.
    }
  }
};
</pre>
              
<h1>The advantage that we have here is that CheckingAccount class the only new pieces we had to add are the fee and we had to redefine a debit to take into consideration the fee, but the other functions credit and getBalance did not have to be redefined cuz we could use them as is and that provides the advantage to using Inheritance when working with similar types of objects.</h1>
 </div>
